<template>
  <div>
    <v-breadcrumbs>
      <v-icon slot="divider">chevron_right</v-icon>

      <v-breadcrumbs-item
      >
        
      </v-breadcrumbs-item>
      <a href="javascript:;" style="
        text-decoration: none;
        font-weight: bold;
      "></a>
    </v-breadcrumbs>
    <v-card class="px-2 mx-1">
      <v-toolbar color="blue darken-3" dark height="48">
        <v-btn dark icon>
          <v-icon>edit</v-icon>
        </v-btn>
        <v-toolbar-title class="ml-0">
          Nhật ký phiên bản
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn dark icon v-on:click.native="backToList">
          <v-icon>reply</v-icon>
        </v-btn>
      </v-toolbar>
      <div class="grid-list mt-3">
          <v-layout wrap>
              <v-flex xs12 md4>
                <v-menu
                    ref="menu1"
                    :close-on-content-click="false"
                    v-model="menu1"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="createDateFomarted"
                      label="Từ ngày"
                      persistent-hint
                      append-icon="event"
                      box
                      @blur="date = parseDate(createDateFomarted)"
                    ></v-text-field>
                    <v-date-picker
                      v-model="createDate"
                      no-title
                      @input="menu1 = false"
                      locale="vi"
                    ></v-date-picker>
                  </v-menu>
              </v-flex>
          </v-layout>
          <v-layout wrap>
              <v-flex xs12>
                <v-data-table
                    :headers="haeders"
                    :items="revesionLogs"
                    class="table-landing table-bordered"
                    hide-actions
                >
                <template slot="items" slot-scope="props">
                    <td>
                       {{(parseInt(page)*10)-10 + props.index +1}} 
                    </td>
                    <td>
                        {{props.item.serviceName}}
                    </td>
                </template>
                <template slot="no-data">
                    <div class="text-xs-center mt-2">
                        Không có dữ liệu
                    </div>
                </template>
                </v-data-table>
              </v-flex>
                <v-flex xs12>
                  <div class="text-xs-right layout wrap" style="position: relative;">
                    <div class="flex pagging-table"> 
                      <tiny-pagination :total="total" :page="page" custom-class="custom-tiny-class" 
                        @tiny:change-page="paggingData" ></tiny-pagination> 
                    </div>
                  </div>
                </v-flex>
          </v-layout>
      </div>
    </v-card>
  </div>
</template>

<script>

import Vue from 'vue'
import $ from 'jquery'
import toastr from 'toastr'
import TinyPagination from './Pagination.vue'

Vue.use(toastr)
export default {
  props: ['index'],
  components: {
    'tiny-pagination': TinyPagination
  },
  data: () => ({
    snackbarerror: false,
    snackbarsuccess: false,
    createDateFomarted: null,
    createDate: new Date().toISOString().substr(0, 10),
    haeders: [   
        {
            text: 'STT',
            align: 'center',
            sortable: false            
        }, 
        {
            text: 'Thủ tục hành chính',
            align: 'center',
            sortable: false
        }
    ],
    revesionLogs: [],
    page: 1,
    total: 0,
  }),
  computed: {

  },
  watch: {
      createDate (val) {
        this.createDateFomarted = this.formatDate(this.createDate)
      }
  },
  created () {
    let vm = this
    vm.$nextTick(function () {
        vm.getRevesionLog()
    })
  },
  watch: {

  },
  methods: {
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}/${month}/${year}`;
    },
    parseDate(date) {
        if (!date) return null;

        const [month, day, year] = date.split("/");
        return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
    },
    getRevesionLog () {
        let vm = this
        let filter = {
            start: vm.page*10 -10,
            end: vm.page*10
        }
        vm.$store.dispatch('getRevesionLog',filter).then(function (result) {
          console.log(result)
          vm.revesionLogs=result.data
          vm.total = result.total
        }).catch(function (){})
    },
    paggingData (config) {
        let vm = this
        vm.page = config.page
        vm.getRevesionLog()
      
    },
  }
}
</script>
